import React from 'react'
import { required } from '../../../../../../utils/validators'
import { Field, reduxForm } from 'redux-form'
import Button from '../../../../../common/Button/Button'
import Input from '../../../../../common/Input/Input'
import Textarea from '../../../../../common/Textarea/Textarea'
import styles from './ProfileDataForm.module.css'

function ProfileDataForm ({ handleSubmit, error, profile, setEditMode }) {
  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      {error && <div className={styles.error}>{error}</div>}

      <h3> </h3>

      <div className={styles.aboutMe}>
        <div>
          <label htmlFor='dataFormName'>:</label>
          <Field
            type='text'
            width='300px'
            placeholder=''
            name='fullName'
            component={Input}
            validate={[required]}
            id='dataFormName'
          />
        </div>

        <div>
          <label htmlFor='dataFormWork'> :</label>
          <Field type='checkbox' name='lookingForAJob' component={Input} id='dataFormWork'/>
        </div>

        <div>
          <label htmlFor='dataFormSkills'> :</label>
          <Field
            rows={2}
            name='lookingForAJobDescription'
            component={Textarea}
            validate={[required]}
            id='dataFormSkills'
            style={{ width: '300px' }}
          />
        </div>

        <div>
          <label htmlFor='dataFormAbout'> :</label>
          <Field
            rows={2}
            style={{ width: '300px' }}
            name='aboutMe'
            component={Textarea}
            validate={[required]}
            id='dataFormAbout'
          />
        </div>
      </div>

      <hr/>

      <h3> </h3>

      <div className={styles.myContacts}>
        {
          Object.keys(profile.contacts).map(key => (
            <div key={key}>
              <label htmlFor={'dataForm' + key}>{key[0].toUpperCase() + key.slice(1)}:</label>
              <Field
                type='text'
                width='300px'
                name={'contacts.' + key}
                component={Input}
                id={'dataForm' + key}
              />
            </div>
          ))
        }
      </div>

      <Button></Button>
      <Button type='button' margin='0 0 0 10px' onClick={() => {setEditMode(false)}}></Button>
    </form>
  )
}

export default reduxForm({ form: 'edit-profile' })(ProfileDataForm)
